$color-list: primaryColor $primaryColor, primaryColor1 $primaryColor1, primaryColor2 $primaryColor2, primaryColor3 $primaryColor3, primaryColor4 $primaryColor4, wdwBlack $wdwBlack, wdwBlack1 $wdwBlack1, wdwBlack2 $wdwBlack2, wdwBlack3 $wdwBlack3, wdwBlack4 $wdwBlack4, wdwGrey $wdwGrey, wdwGrey1 $wdwGrey1, wdwGrey2 $wdwGrey2, wdwGrey3 $wdwGrey3, wdwGrey4 $wdwGrey4, wdwBlue $wdwBlue, wdwBlue1 $wdwBlue1, wdwBlue2 $wdwBlue2, wdwBlue3 $wdwBlue3, wdwBlue4 $wdwBlue4, wdwPurple $wdwPurple, wdwPurple1 $wdwPurple1, wdwPurple2 $wdwPurple2, wdwPurple3 $wdwPurple3, wdwPurple4 $wdwPurple4, wdwOrange $wdwOrange, wdwOrange1 $wdwOrange1, wdwOrange2 $wdwOrange2, wdwOrange3 $wdwOrange3, wdwOrange4 $wdwOrange4, wdwGreen $wdwGreen, wdwGreen1 $wdwGreen1, wdwGreen2 $wdwGreen2, wdwGreen3 $wdwGreen3, wdwGreen4 $wdwGreen4;

@each $color in $color-list {
	[data-themeColors = "#{nth($color, 1)}"] {
		--bg: nth($color, 2);
		@if 66 < lightness(nth($color, 2)) {
			--fg: hsl(hue(nth($color, 2)) , 10, 0);
 }			//--fg: red
		@else {
			@if 33 < lightness(nth($color, 2)) {
				--fg: hsl( hue(nth($color, 2)) , 20, 95);
 }				//--fg: blue
			@else {
				--fg: hsl( hue(nth($color, 2)) , 20, 80);
 } } } }				//--fg: green
@if $live == false {
	[data-themeColors] {
		color: var(--fg);
		background-color: var(--bg); } }
